<template>
  <div style="width: 100%; height: 100vh; background-color: aliceblue; overflow: hidden">
    <div style="width: 400px; margin: 200px auto">

      <div style="color: deepskyblue; padding: 30px; font-size: 30px; text-align: center">欢迎注册</div>
      <div style="margin: 20px"/>
      <el-form
          ref="form"
          :model="form"
          :label-position="'top'"
          label-width="100px"
          style="max-width: 460px"
          :rules="rules"
      >
        <el-form-item label="用户名"  prop="username">
          <el-input v-model="form.username">
            <template #prefix>
              <el-icon>
                <UserFilled/>
              </el-icon>
            </template>
          </el-input>
        </el-form-item>
        <el-form-item label="密码" prop="password" >
          <el-input v-model="form.password" show-password>
            <template #prefix>
              <el-icon>
                <Lock/>
              </el-icon>
            </template>
          </el-input>
        </el-form-item>
        <el-form-item label="确认密码" prop="password" >
          <el-input v-model="form.confirm" show-password>
            <template #prefix>
              <el-icon>
                <Lock/>
              </el-icon>
            </template>
          </el-input>
        </el-form-item>
        <el-button style="width: 100%" type="primary" @click="register">立 即 注 册</el-button>
      </el-form>
    </div>
    <div style="margin: 250px"/>
    <div style="text-align: center; color: aliceblue">
      <el-divider >DouyouknowMiemie</el-divider>
      <el-text size="small" tag="b">Present</el-text>
    </div>
  </div>
</template>

<script>
import {Search, UserFilled, Lock} from "@element-plus/icons";
import request from "@/utils/request";

export default {
  name: "Register",
  components: {
    Search, UserFilled, Lock,
  },
  data() {
    return {
      form: {},
      rules :({
        username: [
          { required: true, message: '请输入用户名', trigger: 'blur' },
        ],
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' },
        ],
      }),
    }
  },
  methods: {
    register() {
      if(this.form.password != this.form.confirm){
        this.$message({
          type:"error",
          message:'两次密码输入不一致'
        })
      }
      this.$refs['form'].validate((valid) => {
        if(valid){
          //此refs对应着表单中的ref='' 本条作用是 当验证rules合法后才会执行下面的request.post语句
          request.post("/user/register", this.form).then(res => {
            if (res.code === '0') {
              this.$message({
                type: "success",
                message: "注册成功"
              })
              this.$router.push("/login")
            } else {
              {
                this.$message({
                  type: "error",
                  message: res.msg
                })
              }
            }
          })
        }
      })

    }
  }
}
</script>

<style scoped>

</style>